import { LOGIN, REGISTER, LOGOUT, ASYNC_START, ASYNC_END } from '@constants';

const defaultState = {
  processing: false
};

export default (state = defaultState, action) => {
  if (action.type === ASYNC_START) {
    //  
    return {
      ...state,
      processing: true
    };
  }

  if (action.type === ASYNC_END) {
    //  
    return {
      ...state,
      processing: false
    };
  }

  return state;
};
